body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
  }
.card{
    border-radius: 10px;
    background-color: lightgrey
}

.search-list{
    border-radius: 15px;
    background-color: lightblue
}

.saveBook {
    border-radius: 5px;
    margin-left: 10px
}

.viewBook {
    border-radius: 5px;
    margin-left: 5px
}
form{
    background: lightblue;
    border-radius: 15px;
}
.BookSearchLabel{
    margin-left:20px;
    margin-top: 10px
}
.submitBtn{
    margin-bottom: 10px;
    margin-left: 20px;
}
.form-control{
    margin-left: 20px;
    width: 95%
}
.navbar {
    background-color: #8a4384;
    font-family: 'Open Sans', sans-serif;
  }
  
  .navbar-brand {
    color: white;
    cursor: default;
  }
  
  #btnSaved,
  #btnSearch {
    color: white;
  }
  
  #btnSaved:hover,
  #btnSearch:hover {
    font-size: 1.05rem;
  }
  
  #btnSaved:active,
  #btnSearch:active {
    font-size: 1rem;
  }
.footer {
    height: 5vh;
    background-color: #8a4384;
    width: 100%;
    /* margin-top: 15vh; */
    /* margin-bottom: 0vh !important; */
}

.wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
}

.builtBy {
    padding-top: 1.2vh;
    color: white;
}
.App {
    text-align: center;
  }
  
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 80px;
  }
  
  .App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
  }
  
  .App-intro {
    font-size: large;
  }
  
  @keyframes App-logo-spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
  }
